<template>
    <div id="MiBalance">
        <h2> {{username}} </h2>
        <h2>Tu saldo es: <span> {{balance}} COP</span></h2>
    </div>
</template>

<script> 
    import axios from'axios';
    export default {
        name: 'MiBalance',
        data: function(){
            return {username:"", balance: 0
            }
        },

        created: function(){
            this.username = this.$route.params.username
            let self = this
            axios.get("http://localhost:8000/user/balance/" + this.username)
                .then((result) => {
                    self.balance = result.data.balance
                })
                .catch((error) => {
                    alert("ERRORServidor");
                });
        }
    }                      
</script>    

<style>
#MiBalance{
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}
#MiBalance h2{
    font-size:50px;
    color:#283747;
}

#MiBalance span{
    color:crimson;
    font-weight:bold;
}
</style>


